import './App.css';
import './components/login.css';
import React from 'react';
import {Button} from 'reactstrap';
import {HashRouter as Router, Switch, Route, Link, Redirect} from 'react-router-dom';
import FbLogin from './components/fbLogin';
import Login from './components/login';
import Logout from './components/logout';

class App extends React.Component {
  state = {
    loggedIn: false,
    accessToken: '',
    loggedIn: false,
  }
  redirectPage = (value) => {
    this.setState({
      loggedIn: value
    })
  }
  setAccess = (number) => {
    this.setState({
      accessToken: number
    })
  }
  render() {
    const {loggedIn, accessToken} = this.state;
    console.log('app access', accessToken, 'windowfb', window.FB)
    const PrivateRoute = ({component: Component, ...rest}) => (
      <Route {...rest} render={(props) => (
        (accessToken !== undefined || window.FB === undefined)
        ? <Component {...props} />
        : <Redirect to={{pathname: '/', state: {from: props.location} }} />
      )}/>
    )
      return (
        <Router basename='/'>

                <Route exact path="/">
                  
                    <div className="form">
                    <div className="rowMine">
                      <Login showBtn={this.showBtn} />
                    </div>
                    </div>
                  
                </Route>       
                <Route path="/fb">
               
                    <div className="form">
                    <div className="rowMine">
                      <FbLogin loggedIn={loggedIn} redirectPage={this.redirectPage} setAccess={this.setAccess} accessToken={accessToken} />
                    </div>
                    </div>
                  
                </Route>
                
                <PrivateRoute component={Logout} path='/dashboard'  />
        
        </Router>
      )
  }
}

export default App;
