import React from 'react';
import {Redirect} from 'react-router-dom';

class FbLogin extends React.Component {
  state = {
    redirect: false
  }
  statusChangeCallback = (response) => {
    const {status} = response;
    console.log(status)
    return new Promise((res, rej) => {
      if (status === 'not_authorized' || status === 'unknown') {
        window.FB.login((response) => {
          this.props.setAccess(response.authResponse.accessToken)
          res(response.authResponse)  
        })
      } else {
        //Should not be showing button in this scenario - just process access token and quickly redirect
        this.props.setAccess(response.authResponse.accessToken)
        res(response.authResponse)
      }
   })
  }
   checkLoginState = () => {
     return new Promise((res, rej) => {
        window.FB.getLoginStatus(function(response) {

            res(response)
        });
      })
    }
  loadFbApi() {
    return new Promise((res, rej) => {
      (function(d, s, id) {
        var js, fjs = d.getElementsByTagName(s)[0];
        if (d.getElementById(id)) return;
        js = d.createElement(s); js.id = id;
        js.src = "//connect.facebook.net/en_US/sdk.js";
        fjs.parentNode.insertBefore(js, fjs);
      }(document, 'script', 'facebook-jssdk'));
      window.fbAsyncInit = function() {
          let FB_APP_ID = 1796391010517069;
          window.FB.init({
              appId      : FB_APP_ID,
              cookie     : true, 
              // the session
              xfbml      : true, 
              version    : 'v9.0' 
          })
          window.FB.AppEvents.logPageView();
          console.log('fired')
          res()
        }
      })
    }

    componentDidMount() {
      console.log('mount', window.FB)
      if (window.FB) {
        console.log('window exists')
        //Could set state to make custom button appear
        //FB button likely not appearing due to window.FB?
          let fire = this.checkLoginState.bind(this)
          fire()
          .then((response) => {
            console.log('res1', response)
            let fire2 = this.statusChangeCallback.bind(this)
            fire2(response)
            .then(res => {
              console.log('res2', res, this.props.accessToken)
              if(this.props.accessToken.length > 1) {
                console.log('here')
                this.setState({redirect: true})
                console.log('redirect', this.state.redirect)
                setTimeout(() => {this.forceUpdate()}, 1000)
              }
            })
          })
      } else {
        this.loadFbApi()
        .then(() => {
          let fire = this.checkLoginState.bind(this)
          fire()
          .then((response) => {
            console.log('res1', response)
            let fire2 = this.statusChangeCallback.bind(this)
            fire2(response)
            .then(res => {
              console.log('res2', res, this.props.accessToken)
              if(this.props.accessToken.length > 1) {
                console.log('here')
                this.setState({redirect: true})
                console.log('redirect', this.state.redirect)
                setTimeout(() => {this.forceUpdate()}, 1000)
              }
            })
          })
        })
      }
    }



    render() {
      const {loggedIn} = this.props;
      const {redirect} = this.state;
      console.log('logged in', loggedIn, redirect)
        return (
            <React.Fragment>
              {redirect === true ? <Redirect to="/dashboard" /> : null}
                <div id="fb-root">
                  {/* <div onClick={this.handleClick}>FACEBOOK */}
                  <div 
                  data-onlogin={() => console.log('data login')}
                  className="fb-login-button" 
                  data-width="300" 
                  data-size="large" 
                  data-button-type="continue_with" 
                  data-layout="default" 
                  data-auto-logout-link="false" 
                  data-use-continue-as="false"
                  scope='read_insights,instagram_basic,pages_show_list,instagram_manage_comments,instagram_manage_insights,pages_read_engagement' 
                  >
                  </div>
                </div>
            </React.Fragment>
        )
    }
}

export default FbLogin;